import React from 'react';
import './App.css';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faFlushed } from '@fortawesome/free-regular-svg-icons';
import { faPlay, faPause, faUndo, faArrowUp, faArrowDown } from '@fortawesome/free-solid-svg-icons';
import Button from 'react-bootstrap/Button';

export default class App extends React.Component {
  // eslint-disable-next-line no-useless-constructor
  constructor(props) {
    super(props);
  }

  render() {
    return (
      <div className="App">
        <Clock />
        <FontAwesomeIcon icon={faFlushed} />
      </div>
    );
  }
}

class Clock extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      time_left: "25:00",
      session_length: 25,
      break_length: 5,
      running: false,
      session_running: true
    }
    this.Start_stop = this.Start_stop.bind(this);
    this.Reset = this.Reset.bind(this);
    this.Increase = this.Increase.bind(this);
    this.Decrease = this.Decrease.bind(this);
    this.CountTimeDown = this.CountTimeDown.bind(this);
    this.TimeEnded = this.TimeEnded.bind(this);
  }

  componentDidMount() {
    let audio = document.createElement("AUDIO");
    audio.id = "beep";
    audio.className = "clip";
    audio.src = 'https://raw.githubusercontent.com/freeCodeCamp/cdn/master/build/testable-projects-fcc/audio/BeepSound.wav';
    audio.type = "audio/mpeg";
    let wrapper = document.getElementById("start_stop");
    wrapper.appendChild(audio);
  }

  Start_stop() {
    this.setState((state) => ({
      running: !state.running
      })
    );
    setTimeout(this.CountTimeDown, 1000);
  }

  CountTimeDown() {
    if (this.state.running) {
      let new_time_left = SubtractSecond(this.state.time_left)
      if (this.state.time_left !== "00:00") {
        this.setState((state) => ({
          time_left: new_time_left
        }));
        setTimeout(this.CountTimeDown, 1000);
      } else {
        this.TimeEnded();
        setTimeout(this.CountTimeDown, 1000);
      }
    }
  }

  TimeEnded() {
    let audio = document.getElementById("beep");
    audio.pause();
    audio.currentTime = 0;
    audio.play();

    let session_running = this.state.session_running;
    if (session_running) {
      this.setState((state) => ({
        session_running: false,
        time_left: stringify(state.break_length)
      }));
    } else {
      this.setState((state) => ({
        session_running: true,
        time_left: stringify(state.session_length)
      }));
    }
  }

  Reset() {
    let audio = document.getElementById("beep");
    if (!audio.paused) {
      audio.pause();
      audio.currentTime = 0;
    }
    this.setState({
      time_left: "25:00",
      session_length: 25,
      break_length: 5,
      running: false,
      session_running: true
    });
  }

  Decrease(button) {
    if (this.state.running){
      return;
    }
    let possible_buttons = [button.target.id, button.target.parentNode.id, button.target.parentNode.parentNode.id];
    if (possible_buttons.indexOf("break-decrement") !== -1 && this.state.break_length > 1) {
      this.setState((state) => ({
        break_length: state.break_length - 1
      }))
    } else if (possible_buttons.indexOf("session-decrement") !== -1 && this.state.session_length > 1) {
      this.setState((state) => ({
        session_length: state.session_length - 1,
        time_left: stringify(state.session_length - 1)
      }))
    }
  }

  Increase(button) {
    if (this.state.running){
      return;
    }
    let possible_buttons = [button.target.id, button.target.parentNode.id, button.target.parentNode.parentNode.id];
    if (possible_buttons.indexOf("break-increment") !== -1 && this.state.break_length < 60) {
      this.setState((state) => ({
        break_length: state.break_length + 1
      }))
    } else if (possible_buttons.indexOf("session-increment") !== -1 && this.state.session_length < 60) {
      this.setState((state) => ({
        session_length: state.session_length + 1,
        time_left: stringify(state.session_length + 1)
      }))
    }
  }

  render() {
    let icon_start_stop = <FontAwesomeIcon icon={faPlay} />;
    let icon_start_stop_text = "Start";
    if (this.state.running) {
      icon_start_stop = <FontAwesomeIcon icon={faPause} />;
      icon_start_stop_text = "Stop";
    }
    return (
    <div id="clock">
      <div className="window">
        <div>25 + 5 Clock</div>
        <div id="lengths">
          <BreakOrSessionLength
            type={"Break"} 
            length={this.state.break_length }
            Increase={this.Increase}
            Decrease={this.Decrease}
          />
          <BreakOrSessionLength
            type={"Session"}
            length={this.state.session_length}
            Increase={this.Increase}
            Decrease={this.Decrease}
          />
        </div>
        <Countdown session_running={this.state.session_running} time_left={this.state.time_left}/>
        <div>
          <Button id="start_stop" className="" onClick={this.Start_stop}>
            {icon_start_stop} <br />
            {icon_start_stop_text}
          </Button>
          <Button id="reset" className="" onClick={this.Reset}>
            <FontAwesomeIcon icon={faUndo} /> <br />
            Reset
          </Button>
        </div>
      </div>
    </div>
    );
  }
};


const BreakOrSessionLength = (props) => {
  let button_dec_id = "";
  let button_inc_id = "";
  let length_id = "";
  if (props.type === "Break") {
    button_dec_id = "break-decrement";
    button_inc_id = "break-increment";
    length_id = "break-label";
  } else {
    button_dec_id = "session-decrement";
    button_inc_id = "session-increment"; 
    length_id = "session-label";
  }
  return (
    <div id="sideways">
      <div id={length_id}>
        {props.type + " Length"}
      </div>
      
      <Button id={button_dec_id} onClick={props.Decrease}>
        <FontAwesomeIcon icon={faArrowDown} /> <br />
      </Button>
      <div id={props.type.toLowerCase() + "-length"}>
        {props.length}
      </div>
      <Button id={button_inc_id} onClick={props.Increase}>
        <FontAwesomeIcon icon={faArrowUp} />
      </Button>
    </div>
  );
};

const Countdown = (props) => {
  let session_or_break = "";
  if (props.session_running) {
    session_or_break = "Session";
  } else {
    session_or_break = "Break";
    
  }
  return (
    <div id="timer">
      <div id="timer-label">
        {session_or_break}
      </div>
      <div id="time-left">
        {props.time_left}
      </div>
    </div>
  );
}

const SubtractSecond = function(time_left) {
  let [minutes, seconds] = time_left.split(":");
  if (seconds[1] === "0") {
    if (seconds[0] === "0") {
      if (minutes[1] === "0") {
        if (minutes[0] === "0") {
          return "00:00";
        } else {
          let new_int = parseInt(minutes[0]) -1;
          return new_int.toString() + "9:59";
        }
      } else {
        let new_int = parseInt(minutes[1]) -1;
        return minutes[0] + new_int.toString() + ":59";
      }
    } else {
      let new_int = parseInt(seconds[0]) -1;
      return minutes + ":" + new_int.toString() + "9";
    }
  } else {
    let new_int = parseInt(seconds[1]) -1;
    return minutes + ":" + seconds[0] + new_int.toString();
  }
}

const stringify = function(time) {
  if (time < 10) {
    return "0" + time.toString() + ":00";
  }
  return time.toString() + ":00";
}